import './App.css';
import Main from './comp/Main';
import photo1 from './img/img1.jpg';
import photo2 from './img/img2.jpg';
import photo3 from './img/img3.jpg';
import photo4 from './img/img4.jpg';
import photo5 from './img/img5.jpg';
import photo6 from './img/img6.jpg';
import photo7 from './img/img7.jpg';
import photo8 from './img/img8.jpg';
import photo9 from './img/img9.jpg';
import photo10 from './img/img10.jpg';
import photo11 from './img/img11.jpg';
import photo12 from './img/img12.jpg';
import { BrowserRouter, Route, Switch, Link } from 'react-router-dom';
import Covid from './comp/Covid';
import Map from './comp/Map';

function App() {

  window.onload = function(){
    const elMenu = document.querySelectorAll('.menu li');
    let k=0;
    function menu(){
      for(let i=0; i<elMenu.length; i++){
        elMenu[i].addEventListener("click",function(){
          elMenu[k].classList.remove('active');
          elMenu[i].classList.add('active');
          k=i;
        })
      }
    }
    menu();

    const elPop = document.querySelectorAll('.popup');
    const elBtn = document.querySelectorAll('.circle');
    function pop(){
      for(let i=0; i<elBtn.length; i++){
        elBtn[i].addEventListener("click",function(){
          elPop[i].classList.add('active');
        })
        elPop[i].addEventListener("click",function(){
          elPop[i].classList.remove('active');
        })
      }
    }
    pop();
  }

  const API_KEY = "b7d0c5bc7ada2e64748be79d7b952fa6";
    
    function onGeoOk(position){
        const lat =position.coords.latitude;
        const lng =position.coords.longitude;
        const url = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lng}&appid=${API_KEY}&units=metric`;
        fetch(url)
        .then((response) => response.json())
        .then((data) => {
            const weather = document.querySelector(".weather");
            const temp = document.querySelector(".temp");
            const city = document.querySelector(".city");
            city.innerText = data.name;
            weather.innerText = `${data.weather[0].main}`;
            temp.innerText = ` ${Math.floor(data.main.temp)}℃`;
        });
    };
    function onGeoError(){
        alert("can't find you")
    };
    navigator.geolocation.getCurrentPosition(onGeoOk,onGeoError);

  const imgSrc=[
    {img:photo1,tit:"title1",sub:"리액트 컴포넌트로 뿌려준 항목 01"},
    {img:photo2,tit:"title2",sub:"리액트 컴포넌트로 뿌려준 항목 02"},
    {img:photo3,tit:"title3",sub:"리액트 컴포넌트로 뿌려준 항목 04"},
    {img:photo4,tit:"title4",sub:"리액트 컴포넌트로 뿌려준 항목 04"},
    {img:photo5,tit:"title5",sub:"리액트 컴포넌트로 뿌려준 항목 05"},
    {img:photo6,tit:"title6",sub:"리액트 컴포넌트로 뿌려준 항목 06"},
    {img:photo7,tit:"title7",sub:"리액트 컴포넌트로 뿌려준 항목 07"},
    {img:photo8,tit:"title8",sub:"리액트 컴포넌트로 뿌려준 항목 08"},
    {img:photo9,tit:"title9",sub:"리액트 컴포넌트로 뿌려준 항목 09"},
    {img:photo10,tit:"title10",sub:"리액트 컴포넌트로 뿌려준 항목 10"},
    {img:photo11,tit:"title11",sub:"리액트 컴포넌트로 뿌려준 항목 11"},
    {img:photo12,tit:"title12",sub:"리액트 컴포넌트로 뿌려준 항목 12"}
  ];
  
  
  
  return (
    <BrowserRouter>
    <header className="header">
      <div className="logo">
        SUN space
        <p className="subtit">- react page -</p>
      </div>
      <ul class="menu">
      <Link to="/react/"><li className="active">HOME</li></Link>
      <Link to="/covid"><li>COVID-19</li></Link>
        <Link to="/map"><li>MAP</li></Link>
        <Link to="/react/"><li>메뉴 준비중...</li></Link>
      </ul>
      <div class="current_weather">
        <p>Today's Weather</p>
        <div class="city"></div>
        <div class="wea-tmp">
            <span class="weather"></span>
            <span class="temp"> </span>
        </div>
      </div>
    </header>
      <Switch>
    <main>
        <Route exact path="/react/">
          <ul>
            {
              imgSrc.map( (src,k) => (
                <Main data={src} key={k}/>
              ) )
            }
          </ul>
        </Route>
        <Route path="/covid"><Covid/></Route>
        <Route path="/map"><Map/></Route>
    </main>
      </Switch>
    </BrowserRouter>
  );
}

export default App;
